.currency-div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    width: 450px;
    height: auto;
    border: 1px solid white;
    border-radius: 10px;
}


.amount {
    width: 80px;
    height: 25px;
    margin-right: 10px;
    border-radius: 6px;
}

.from-currency-option {
    width: 80px;
    height: 25px;
    margin-right: 10px;
    border-radius: 6px;
}

.to-currency-option {
    width: 80px;
    height: 25px;
    margin-right: 10px;
    border-radius: 6px;
}

.result {
    width: 80px;
    height: 25px;
    margin-right: 10px;
    border-radius: 6px;
}

.convert-btn {
    background-color: white;
    width: 80px;
    height: 40px;
    padding: 5px;
    border: 1px solid black;
    border-radius: 10px;
    margin-top: 10px;
    cursor: pointer;
    margin-bottom: 10px;
}

@media only screen and (max-width :437px) {
    .currency-div {
        /* width: 22rem; */
        width: 88%;
        height: auto;
    }
}