.currency-Status-div {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
    border: 1px solid white;
    border-radius: 8px;
    width: 450px;
}

@media only screen and (max-width :437px) {
    .currency-Status-div {
        width: 88%;
        height: auto;
    }
}